.App {
  text-align: center;
}

.black-nav {
  background-color: black;
  color: white;
  display: flex;
  padding-left: 20px;
  width: 100vw;
  font-size: 1.3rem;
}

div {
  box-sizing: border-box;
}
.list {
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid grey;
}

span:hover {
  cursor: pointer;
}

.modal {
  background-color: powderblue;
  width: 80vw;
  margin: auto;
  padding: 10px;
  margin-top: 20px;
  text-align: left;
}

h4 > .mouse {
  color: red;
  border: 1px solid red;
}
